<template>
  <div>
    <header class="per_center mb0">
      <div class="pre_title">
        <a href="javascript:void(0)" class="icon-Artboard7" @click="isOut=true"></a>
      </div>
    </header>
    <div class="fixBox1">
      <div class="order_msg mb15">
        <h4>{{indexDate.roomTypeName}}</h4>
        <div class="msg_mian">
          <div class="msg_l">
            <div class="limit-word">{{indexDate.pricePlanName}}</div>
            <p>{{model.bedTypeDesc}} <span>|</span>
              {{model.commodityItemDTOList&&model.commodityItemDTOList[0].breakfastNum|breakfastFilter}}</p>
            <p>{{model.checkInDate|dateFilter}}入住 － {{model.checkOutDate|dateFilter}}离店 共<i>{{roomnight}}</i>晚</p>
          </div>
          <div class="msg_r">
            <div class="msg_r_img">
              <img :src="indexDate.imgUrls" @error="errorLoadImg" alt="">
            </div>
          </div>
        </div>
        <div class="order_status">
          <i class="icon-Artboard3"></i>
          <span
            v-if="model.restrictDTO">{{model.restrictDTO&&RestrictTypeList[model.restrictDTO.cancelRestrictType+1]}}</span>
          <span>{{model.canImmediate==1?'即时确认':'待确认'}}</span>
          <span>{{model.payMethod=='PRE_PAY'?'在线付':'到店付'}}</span>
        </div>

        <!--担保-->
        <p class="order_status order_status_hauto" v-if="guaranDate.guaranteeFeeInfo">
          <i class="icon-Artboard3"></i>
          <span id="txtCall">该产品需要提供 {{model.settleCurrencyEnum}}{{guaranDate.guaranteeFee}} 的担保金额,<i class="colorf60 ">请联系酒店</i>,完成线下担保！</span>
        </p>
      </div>
      <inputStep v-if="model.limitArr.length>0"
                 v-model="roomQuantity" :limitNum="model.limitArr"
                 :showSubtitle="showSubTitle" modalTitle="选择房间数量"
                 :showLast="true" :modalFun="callTel" text="房间数">
        <p @click="callTel">若需5间以上,<i style="color: red;">请联系酒店客服</i></p>
      </inputStep>
      <!--同行-->
      <template v-if="model.userType==1">
        <!--信息录入-->
        <inputText v-if="model.isEngGuest&&model.isEngGuest==1&&stepFlag"
                   v-model="req_booking.guestDTOList[0].guestName"
                   cladss="iptStyle"
                   @validate="validate[1]=true"
                   @contentChg="contentChg1"
                   title="入住人"></inputText>
        <!--港澳客人-->
        <inputText v-if="model.isEngGuest&&model.isEngGuest!=1&&stepFlag"
                   v-model="req_booking.guestDTOList[0].guestName"
                   @validate="validate[1]=true"
                   @contentChg="contentChg1"
                   class="iptStyle"
                   title="入住人" placeholders="姓/名 拼音 例:huang/xiaoming">
        </inputText>
      </template>
      <!--散客-->
      <template v-if="model.userType==2">
        <!--信息录入-->
        <inputText v-if="model.isEngGuest&&model.isEngGuest==1&&stepFlag"
                   v-model="req_booking.guestDTOList[0].guestName"
                   cladss="iptStyle" :val="model.checkInName"
                   @validate="validate[1]=true"
                   @contentChg="contentChg1"
                   title="入住人"></inputText>
        <!--港澳客人-->
        <inputText v-if="model.isEngGuest&&model.isEngGuest!=1&&stepFlag"
                   v-model="req_booking.guestDTOList[0].guestName"
                   @validate="validate[1]=true"
                   @contentChg="contentChg1"
                   class="iptStyle" :val="model.checkInName"
                   title="入住人" placeholders="姓/名 拼音 例:huang/xiaoming">
        </inputText>
      </template>

      <!--散客没有联系人 userType 1同行 2散客-->
      <inputText v-model="req_booking.contacterDTO.contactName" :val="model.linker"
                 v-if="model.userType!=2&&stepFlag" class="iptStyle"
                 @validate="validate[2]=true" @contentChg="contentChg2"
                 title="联系人">
      </inputText>

      <!--同行-->
      <template v-if="model.userType==1">
        <inputText v-model="req_booking.contacterDTO.contactPhone" :val="model.linkerMobilePhone"
                   class="iptStyle" title="联系电话"
                   @validate="validate[3]=true" v-if="stepFlag1"
                   @contentChg="contentChg2"
                   type="tel">
        </inputText>
      </template>

      <!--散客-->
      <template v-if="model.userType==2">
        <inputText v-model="req_booking.contacterDTO.contactPhone" :val="model.mobile"
                   class="iptStyle" title="联系电话"
                   @validate="validate[3]=true" v-if="stepFlag1"
                   @contentChg="contentChg3"
                   type="tel">
        </inputText>
      </template>


      <!--<p>{{model.linkerMobilePhone}}{{model.linker}}</p>-->

      <div class="orderFill_p02 borderTop borderBottom bb" @click="chgFill">
        <span class="colorgray ">特殊要求</span>
			  <span class="sw_ctr" ref="sw_ctr">
			  	<span class="base"></span>
			  	<span class="btn"></span>
			  </span>
      </div>
      <div class="mnore_Div  animated fadeIn">
        <p class="colorf60">尽量满足您的要求，实际以酒店提供为准</p>
        <div class="div1 clearfix">
          <button class="">尽量安排大床房</button>
          <button class="">尽量双床房</button>
          <button class="">尽量安排安静房</button>
          <button class="">尽量安排无烟房</button>
          <button class="">尽量安排高层楼层</button>
          <button class="">尽量安排相邻</button>
        </div>
        <p class="orderFill_p02_O pr dn">
          <span class="orderFill_inp01 bg_sanjiao">当日<em>19:00-20:00</em></span>
          <span>您所选的是酒店地时间</span>
        </p>
        <div class="div">
          <textarea placeholder="请输入其他特殊要求" v-model="req_booking.remark"></textarea>
        </div>
      </div>
      <div class="add_msg" v-if="additionList.length>0">
        <a href="javascript:void(0)" class="add_msg_head" @click="destinSome">
          <span>附加费用</span>
          <i class="icon-Artboard6"></i>
        </a>
        <div class="add_msg_body" v-for="item in req_booking.additionChargeDTOList">
          <p>
            <span class="msg_body_l">{{item.name}}</span>
            <span class="msg_body_c">{{item.num}}份/天,{{item.days}}天</span>
            <span class="msg_body_r"><i>{{model.settleCurrencyEnum}}</i>{{item.num*item.days*item.price}}</span>
          </p>
        </div>
      </div>
      <div class="mnore_infDiv">
        <p class="orderFill_p02">
          <span class="colorgray">姓名<i class="notice_x">*</i></span>
          <input type="text" class="orderFill_inp01" value="" placeholder="请输入您的姓名">
        </p>
        <p class="orderFill_p02">
          <span class="colorgray">邮箱<i class="notice_x">*</i></span>
          <input type="text" class="orderFill_inp01" value="" placeholder="请输入正确的邮箱地址">
        </p>
      </div>

      <!--提示信息-->
      <div class="p_tb03rem">
        <div class="To_travel_article Tnot1" v-for="item in model.pricePlanAdditionalInfoList">
            <p v-if="item.additionalInfoType!=7 && item.additionalInfoType!=8"><span>{{additionalInfoTypeList[item.additionalInfoType-1]}}</span>{{item.content}}</p>
        </div>
      </div>
      <p class="h100"></p>
    </div>
    <footer class="orderFill_bottom_p">
      <span @click="costDetail" class="orderFill_bottom_p_span">总额<span
        class="cur">{{model.settleCurrencyEnum}}<i>{{addtionCost+roomPrice}}</i></span><span
        class="colorf60 ft1s">明细</span></span>
      <a v-if="model.buttonType==2" href="javascript:void(0)"
         class="blueBtn fr J_Reminder"
         :class="[{'op4':!isSub}]"
         @click="booking">去支付</a>
      <a v-if="model.buttonType==1" href="javascript:void(0)"
         class="blueBtn fr J_Reminder"
         :class="[{'op4':!isSub}]"
         @click="booking">提交订单</a>
      <a v-if="model.buttonType==3" href="javascript:void(0)"
         class="blueBtn fr J_Reminder"
         :class="[{'op4':!isSub}]"
         @click="isBtype=true">线下支付</a>
    </footer>


    <div class="hide_body02"></div>

    <!--<div id="notice_model" class="notice-skin">-->
      <!--<span>-->
        <!--<p>若需5间以上 请联系酒店</p>-->
      <!--</span>-->
      <!--<sapn id="tel">Call</sapn>-->
    <!--</div>-->

    <!--附加项-->
    <transition name="slideUD">
      <div class="destinSomeContent" v-if="isDestinSome">
        <header class="per_center mb0">
          <div class="pre_title">
            <a href="javascript:void(0)" class="icon-ArtboardCopy Return-ds" @click="closeDestinSome"></a>
          </div>
          <div class="order_msg">
            <h4 class="fw7 mt7">选择附加项</h4>
          </div>
        </header>
        <div class="ds-scoll">
          <div class="badChose" v-for="(additem,idx) in additionList" :data-type="additem.type">
            <div class="badChose_head">
              <div class="badChose_head2">
                <span><i class="iconflag icon-Artboard11"></i>{{additem.name}}</span>
                <span>
                    <i class="ft1s a1">价格</i>
                    <i class="ft1s">{{model.settleCurrencyEnum}}</i>
                    <i class="ft3">{{additem.price*additem.num*additem.days}}</i>
                </span>
              </div>
              <div class="badChose_head1">
                <div class="badChose_head1_l" @click="checkaddation(additem,$event)">
                </div>
                <div class="badChose_head1_r" @click="checkaddation2($event)"></div>
              </div>
            </div>
            <div class="badChose_body openFlag">
              <inputStep class="badChose_body_s slideInDown"
                         :limitNum=[1,99] v-model="additem.num" :optionNum="99"
                         :modalTitle="additem.name" text="份/天"></inputStep>
              <inputStep class="badChose_body_s slideInDown" v-model="additem.days" :optionNum="99"
                         :modalTitle="additem.name"
                         :limitNum=[1,roomnight] text="天"></inputStep>
            </div>
          </div>
        </div>
        <footer class="orderFill_bottom_p">
          <a href="javaScript:void(0)" class="allBtn J_Reminder" @click="isDestinSome=false">
            确定(<i class="ft1s mr0">{{model.settleCurrencyEnum}}</i>{{addtionCost}})</a>
        </footer>
      </div>
    </transition>

    <!--费用明细-->
    <transition name="slideUD">
      <div class="bedmsg" v-if="iScostDetail">
        <header class="per_center mb0 ds-head">
          <div class="pre_title"><a class="icon-ArtboardCopy" @click="iScostDetail=false"></a></div>
          <h3 class="mt35">费用明细</h3>
        </header>
        <section class="ds-body">
          <ul class="cost-d-list clearfix">
            <li>
              <span>房费</span>
            </li>
            <li v-for="seal in model.commodityItemDTOList">
              <span>{{seal.saleDate}}</span>
              <span><i
                class="ft1">{{model.settleCurrencyEnum}}</i> {{seal.salePrice}} × {{model.roomQuantity}} </span>
            </li>
            <li class="mb15">
              <span>房费小计</span>
              <span><i class="ft1">{{model.settleCurrencyEnum}}</i>{{roomPrice}}</span>
            </li>
          </ul>
          <ul class="cost-d-list clearfix" v-if="req_booking.additionChargeDTOList.length>0">
            <li>
              <span>附加费</span>
            </li>
            <li v-for="item in req_booking.additionChargeDTOList">
              <span>{{item.name}}</span>
              <span><i
                class="ft1">{{model.settleCurrencyEnum}}</i> {{item.price}} × {{item.num}}份/天 {{item.days}}天</span>
            </li>
            <li class="mb15">
              <span>附加费小计</span>
              <span><i class="ft1">{{model.settleCurrencyEnum}}</i>{{addtionCost}}</span>
            </li>
          </ul>
          <div class="Totalnum">
            <span>总计</span>
            <span><i class="ft1">{{model.settleCurrencyEnum}}</i>{{addtionCost+roomPrice}}</span>
          </div>
        </section>
        <div to="/pay" class="ds-foot " @click="iScostDetail=false">关闭
          <Ripple></Ripple>
        </div>
      </div>
    </transition>

    <modal :onOk="cancel" :isShow="isOut" :iShead="false" @close="isOut=false">
      <div class="modal-txt">是否放弃本次预订?</div>
    </modal>

    <modal :onOk="goPay" :isShow="isSubmit" :iShead="false" @close="isSubmit=false">
      <div class="modal-txt">价格变动,由¥898变为¥998</div>
      <div class="modal-txt">是否继续预订?</div>
    </modal>

    <modal :onOk="booking" :isShow="isBtype" :iShead="false" @close="isBtype=false">
      <div class="modal-txt">目前仅支持人民币在线交易</div>
      <div class="modal-txt">请联系酒店线下付款?</div>
    </modal>

    <!--<modal :isShow="isbg" :iSfoot="false" :iShead="false" class="bg1">-->
    <!--<div class="load_body"></div>-->
    <!--</modal>-->

  </div>
</template>


<style>
  .bg1 .f-model-card {
    width: 30% !important;
    background-color: #ccc !important;
  }
</style>
<style scoped>
  .mb-5 {
    margin-top: -0.5rem !important;
  }

</style>
<script>
  import moment from 'moment'
  import {commonInit,getnotice} from '../common'
  import commDate from '../comm.json'
  export default {
    data () {
      return {
        abc: '',
        isbg: true,
        F_addMsg: false,
        stepFlag: false,
        stepFlag1: false,
        iScostDetail: false,
        isDestinSome: false,
        isSubmit: false,
        isOut: false,
        isBtype: false,
        roomnight: '',
        num: '',
        roomPrice: 0,
        addtionCost: 0,
        roomQuantity: 1,
        limitMax: '',
        limitMin: '',
        limitArr: [],
        showSubTitle: true,
        tempArr: [],
        bookingFlag: true,
        /*tool*/
        additionalInfoTypeList: ['特别提示', '提示信息', '促销信息', '预订条款', '入住条款', '取消条款', '预订间数条款'],
        RestrictTypeList: ['可取消', '不可取消', '限时取消'],
        validate: {
          '1': false,
          '2': false,
          '3': false,
        },
        isSub: false,

        guaranCost: '',

        /*req res*/
        req_booking: { //
          roomQuantity: 1,//预订间数
          remark: "",//备注
          orderSum: "",//订单总额
          checkInDate: "",//入住时间
          checkOutDate: "",//离店时间
          payMethod: "",//支付方式
          roomTypeId: "",//房型id
          additionChargeDTOList: [],//附加项
          guestDTOList: [
            {
              phone: '',
              email: '',
              guestName: '',//入住人姓名
              firstName: '',//港澳姓
              lastName: '',//港澳名
            }
          ],//宾客
          isImmediatelyConfirm: "",//是否立即确认 1 2待确认
          supplyWay: "",//产品供应方式
          commodityId: "",//商品id
          hotelId: "",//酒店id
          contacterDTO: {//
            contactEmail: '',
            contactName: '',
            contactPhone: '',
          },//联系人
          pricePlanId: "",//价格计划id
          bedDesc: "",//床型描述
          roomRateDetailDTOList: [],//商品详情
          bedTypeIds: "",//床型ids
          token: '',
          guaranteeMethod: 3,//担保方式
          guaranteeFee: ''//担保金额
        },

        additionitem: {
          "additionChargeId": '',//附件项id
          "priceOrg": 0,
          "price": 10,
          "num": 1,
          "name": "网费",
          "days": 1,
          "currency": "1",
          "pricePlanId": '',//价格计划id
          "type": 3
        },

        guaranDate: {
          guaranteeFee: '',
          guaranteeFeeInfo: '',
        },
        additionList: [],

        model: {
          limitArr: [],
        },
        indexDate: '',
      }
    },
    filters: {
      breakfastFilter(type){
        var breakfastNumArr = commDate.breakfastNumArr;
        // var breakfastNumArr = ["床位早", "无早", "单早", "双早", "三早", "四早", "五早", "六早", "七早", "八早", "九早", "多早", "美式早", "全食宿", "特惠全食宿", "含早", "含1早1份半餐", "早午餐", "欧陆早", "自助早餐", "全食宿", "含1份半餐1份全餐", "英式早", "半食宿", "爱尔兰早", "半食宿+饮料", "全食宿+饮料", "不含早", "苏格兰早", "膳食自理", "十早", "十一早", "十二早", "十三早", "十四早", "十五早"];
        if (type !== '') return breakfastNumArr[type + 1];
      },
      dateFilter(date){
        if (date) {
          var arr = date.split('-')
          return arr[1] + '/' + arr[2]
        }
      },
      numFilter(date1, date2){
        return moment(date1).diff(moment(date2), 'days')
      },
    },
    watch: {
//      validate:{
//        handler:function(val){
//          if(val[1]&&val[2]&&val[3]){
//            this.isSub = true;
//          }else{
//            this.isSub = false;
//          }
//        },
//        deep:true
//      },
      roomQuantity(val, oldVal){
        var _t = this;
        this.req_booking.roomQuantity = val;
        //计算房费
        if (val != oldVal) {
          _t.roomPrice = 0;
          if (_t.model.commodityItemDTOList) {
            _t.model.commodityItemDTOList.forEach(function (vals, idx) {
              _t.roomPrice += parseInt(vals.salePrice * val);
            })
          }
          _t.getGuaran();
        }
      },
      req_booking: {
        handler: function (val, oldVal) {
          var _t = this;
          /*计算总附加费*/
          _t.addtionCost = 0;
          val.additionChargeDTOList.forEach(function (vals, idx) {
            _t.addtionCost += vals.price * vals.num * vals.days
          });
          if (this.model.userType == 2 && _t.req_booking.guestDTOList[0].guestName && _t.req_booking.contacterDTO.contactPhone.length == 11 || this.model.userType == 1 && _t.req_booking.guestDTOList[0].guestName && _t.req_booking.contacterDTO.contactName && _t.req_booking.contacterDTO.contactPhone.length == 11) {
            this.isSub = true;
          } else {
            this.isSub = false;
          }
        },
        deep: true
      }
    },
    created: function () {//初始化2
      commonInit();
      this.Qinfo();

      var _t = this;
      setTimeout(function () {
        _t.stepFlag = true
      }, 500)
      setTimeout(function () {
        _t.stepFlag1 = true
      }, 550)
    },
    mounted: function () {
      var _t = this;
      setTimeout(function () {
        $(".div1 button").on("click", function () {
          $(this).toggleClass("click_btn");
        });
        $('#txtCall').click(function(){
          console.log(_t.indexDate.telePhone);
          window.location.href = 'tel:' + _t.indexDate.telePhone;
        })
      }, 600)
    },

    methods: {
      /*验证*/
      contentChgSameCode(){
        if (this.model.userType == 2 && this.req_booking.guestDTOList[0].guestName && this.req_booking.contacterDTO.contactPhone.length == 11 || this.model.userType == 1 && this.req_booking.guestDTOList[0].guestName && this.req_booking.contacterDTO.contactName && this.req_booking.contacterDTO.contactPhone.length == 11) {
          this.isSub = true;
        } else {
          this.isSub = false;
        }
      },
      contentChg1(val){
        console.log(val);
        this.req_booking.guestDTOList[0].guestName = val;
        this.contentChgSameCode()
      },
      contentChg2(val){
        console.log(val);
        this.contentChgSameCode()
      },
      contentChg3(val){
        console.log(val);
        this.contentChgSameCode()
      },

      /*req res*/
      Qinfo(){
        var _t = this;
        var obj = this.$route.query;
        _t.indexDate = obj;
        obj.firstDayQuotaNum > 5 ? this.limitMax = 5 : this.limitMax = obj.firstDayQuotaNum;
        var data = {
          pricePlanId: obj.pricePlanId,
          pricePlanName: obj.pricePlanName,
          commodityId: obj.commodityId,
          roomTypeId: obj.roomTypeID,
          roomTypeName: obj.roomTypeName,
          checkInDate: obj.checkInDate,
          checkOutDate: obj.checkOutDate,
          roomQuantity: 1,
          firstDayQuotaNum: obj.firstDayQuotaNum,
          payMethod: obj.payMethod,
        };

        this.$ajax.post('/hotel/order/preBooking', data).then(function (res) {
          if (res.data.result == 1) {
            _t.model = res.data.model;
            _t.getGuaran()
            if (!!_t.model.roomQuantity) {
              _t.limitMin = _t.model.roomQuantity
              _t.showSubTitle = true
            } else {
              _t.limitMin = 1
              _t.showSubTitle = false
            }
            _t.limitArr.push(_t.limitMin)
            _t.limitArr.push(_t.limitMax)
            _t.model.limitArr = _t.limitArr
            if (res.data.model.canBooking == 3) {//不可订
              getnotice('暂不可订')
              _t.$router.push('/index')
            } else if (res.data.model.canBooking == 2) {//变价
              alert('产品变价')
            }
            else if (res.data.model.canBooking == 1) {//可订
              book()
            }

            function book() {
              _t.roomnight = moment(_t.model.checkOutDate).diff(moment(_t.model.checkInDate), 'days')
              /*数据同步*/
              _t.req_booking.hotelId = _t.model.hotelId;
              _t.req_booking.roomQuantity = _t.model.roomQuantity;
              _t.req_booking.pricePlanId = obj.pricePlanId;
              _t.req_booking.checkInDate = _t.model.checkInDate;
              _t.req_booking.checkOutDate = _t.model.checkOutDate;
              _t.req_booking.commodityId = _t.model.commodityId;
              _t.req_booking.roomTypeId = _t.model.roomTypeId;
              _t.req_booking.payMethod = _t.model.payMethodKey;
              _t.req_booking.bedTypeIds = _t.model.bedTypesIds;
              _t.req_booking.bedDesc = _t.model.bedTypeDesc;
              _t.req_booking.isImmediatelyConfirm = _t.model.canImmediate;

              if (_t.model.userType == 2) {
                _t.req_booking.guestDTOList[0].guestName = _t.model.checkInName;
              }

              if (_t.model.userType == 1) {//同行
                _t.req_booking.contacterDTO.contactName = _t.model.linker;
                _t.req_booking.contacterDTO.contactPhone = _t.model.linkerMobilePhone;

              } else if (_t.model.userType == 2) {//散客
                _t.req_booking.contacterDTO.contactPhone = _t.model.mobile;
              }
              _t.req_booking.supplyWay = _t.model.supplyWay;
              _t.req_booking.token = _t.model.token;


              /*获取附加项信息*/
              _t.additionList = [];
              if (_t.model.additionChargeIncomeList) {
                _t.additionList = _t.model.additionChargeIncomeList.map(function (val) {
                  return {
                    "additionChargeId": val.additionChargeId,//附加项id
                    "priceOrg": '',
                    "price": val.salePrice,
                    "name": val.additionChargeName,
                    "currency": _t.model.settleCurrencyEnum,
                    "pricePlanId": val.pricePlanId,//价格计划id
                    "type": val.additionChargeType,
                    "num": 1,
                    "days": 1,
                  }
                })
              }

              /*商品详情列表*/
              _t.req_booking.roomRateDetailDTOList = _t.model.commodityItemDTOList.map(function (item) {
                return {
                  breakfastNum: item.breakfastNum,
                  salePrice: item.salePrice,
                  saleDate: item.saleDate,
                  currency: item.baseCurrency,
                  basePrice: item.basePrice,
                  fullPrice: item.salePrice,
                }
              })
              /*初始房费计算*/
              res.data.model.commodityItemDTOList.forEach(function (vals, idx) {
                _t.roomPrice += vals.salePrice * _t.model.roomQuantity;
              })
              var avgPrice = Math.ceil((_t.roomPrice / res.data.model.commodityItemDTOList.length) / _t.model.roomQuantity);
              var str = avgPrice + '-' + _t.indexDate.salePrice
              if (avgPrice != _t.indexDate.salePrice) {
                getnotice('请注意价格变动')
                //return
              }
              if (this.model.userType == 2 && _t.req_booking.guestDTOList[0].guestName && _t.req_booking.contacterDTO.contactPhone.length == 11 || this.model.userType == 1 && _t.req_booking.guestDTOList[0].guestName && _t.req_booking.contacterDTO.contactName && _t.req_booking.contacterDTO.contactPhone.length == 11) {
                _t.isSub = true;
              } else {
                _t.isSub = false;
              }
            }
          } else if (res.data.result == 0) {
            setTimeout(function () {
              _t.$router.push('/index')
            }, 1000)
          }
        }).catch(function (err) {
          //console.log(err, 'err');
        });
      },

      getGuaran(){
        var _t = this;
        var data = {
          checkInDate: this.model.checkInDate,
          checkOutDate: this.model.checkOutDate,
          commodityId: this.model.commodityId,
          channelCode: '',
          distributorCode: '',
          arrivalTime: this.model.arrivalEarlyTime + '-' + this.model.arrivalLateTime,
          roomNum: this.roomQuantity,
          commodityItemDTOList: this.model.commodityItemDTOList,
        }
        _t.$ajax.post('/hotel/order/validateGuaranteeFee', data).then(function (res) {
          if (res.data.result == 1) {
            _t.guaranDate = res.data.model;
            _t.req_booking.guaranteeFee = res.data.model.guaranteeFee;
            // $('#txtCall').unbind('click');
            // $('#txtCall').click(function(){
            //   window.location.href = 'tel:' + _t.indexDate.telePhone;
            // })
          }
        }).catch(function (err) {

        });
      },

