        if (_t.isSub) {
          F.loading.show();
          _t.getDate();
          var data = JSON.parse(JSON.stringify(this.req_booking));
          if ($('.sw_ctr').hasClass('on')) {
            var str = [];
            data.remark ? str.push(data.remark) : ''
            $('.div1 button').each(function () {
              if ($(this).hasClass('click_btn')) {
                str.push($(this).text())
              }
            });
            data.remark = str.join(',')
          } else {
            data.remark = ''
          }
          console.log(JSON.stringify(data));
          if (this.bookingFlag) {
