                var result = res.data.model;
                if (_t.model.buttonType == 1 || _t.model.buttonType == 3) {
                  F.loading.hide();
                  getnotice('下单成功');
                  _t.$router.push('/Order/' + result.orderCode +'/0');
                } else if (_t.model.buttonType == 2) {

                  var host = window.location.host;
                  window.location.href = 'http://' + host + '/miceMobile/payPage.html?orderCode='+result.orderCode+'&token='+result.token;
                  // getnotice('下单成功,正在跳转支付...');
                  // function createHideInput(name, value) {
                  //   var newElement = document.createElement("input");
                  //   newElement.setAttribute("name", name);
                  //   newElement.setAttribute("type", "hidden");
                  //   newElement.setAttribute("value", value);
                  //   return newElement
                  // }

                  // function payHotelOrder() {
                  //   var data={orderCode:result.orderCode,token:result.token};
                  //   data = JSON.stringify(data);
                  //   var host = window.location.host;
                  //   var tempwindow = window.open();
                  //   $.ajax({
                  //     url: 'http://' + host + '/mice-mobile/hotel/order/getPayParam',
                  //     type: 'post',
                  //     data: data,
                  //     dataType: 'json',
                  //     async: false,
                  //     contentType: "application/json",
                  //     success: function (data) {

                  //       getnotice('生成支付界面中.......');
                  //       if (data.result == 1) {
                  //         F.loading.hide();
                  //         var payForm = document.createElement("form");
                  //         document.body.appendChild(payForm);
                  //         payForm.method = 'post';
                  //         payForm.action = 'http://www.fangcang.com/titanjr-pay-app/titanPay.action';
                  //         payForm.appendChild(createHideInput("payType", data.model.payType));
                  //         payForm.appendChild(createHideInput("payeeOrg", data.model.payeeOrg));
                  //         payForm.appendChild(createHideInput("payeeOrgName", data.model.payeeOrgName));
                  //         payForm.appendChild(createHideInput("payerOrgName", data.model.payerOrgName));
                  //         payForm.appendChild(createHideInput("commodityName", data.model.commodityName));
                  //         payForm.appendChild(createHideInput("commodityDesc", data.model.commodityDesc));
                  //         payForm.appendChild(createHideInput("bussOrderNo", data.model.bussOrderNo));
                  //         payForm.appendChild(createHideInput("currencyType", data.model.currencyType));
                  //         payForm.appendChild(createHideInput("amount", data.model.amount));
                  //         payForm.appendChild(createHideInput("channelType", data.model.channelType));
                  //         payForm.appendChild(createHideInput("notifyUrl", data.model.notifyUrl));
                  //         payForm.appendChild(createHideInput("bussInfo", data.model.bussInfo));
                  //         payForm.appendChild(createHideInput("successJumpUrl", data.model.successJumpUrl));
                  //         payForm.appendChild(createHideInput("failJumpUrl", data.model.failJumpUrl));
                  //         payForm.appendChild(createHideInput("escrowedDate", data.model.escrowedDate));
                  //         payForm.appendChild(createHideInput("sign", data.model.sign));
                  //         getnotice('支付中.......');
                  //         payForm.submit()
                  //       } else {
                  //         F.loading.hide()
                  //       }
