                  //     }
                  //   })
                  // }
                  // payHotelOrder();
                }
              } else {
                setTimeout(function () {
                  F.loading.hide();
                  _t.$router.push('/index')
                }, 500)
              }
            }).catch(function (err) {
              setTimeout(function () {
                F.loading.hide();
                _t.bookingFlag = true
              }, 700)
            })
          }
        }
      },

      getDate(){//获取下单数据
        var _t = this;
        /*港澳入住人*/
        if (this.model.isEngGuest != 1) {
          var guest = this.req_booking.guestDTOList[0].guestName.split('/');
          this.req_booking.guestDTOList[0].firstName = guest[0]
          this.req_booking.guestDTOList[0].lastName = guest[1]
        }
        //联系人处理
        if (_t.model.userType == 2) {
          _t.req_booking.contacterDTO.contactName = _t.req_booking.guestDTOList[0].guestName;
        }
        //入住人电话处理
        _t.req_booking.guestDTOList[0].phone = _t.req_booking.contacterDTO.contactPhone
        /*计算订单总额*/
        this.req_booking.orderSum = this.addtionCost + this.roomPrice;
        //获取商品详情
        _t.req_booking.roomRateDetailDTOList = _t.model.commodityItemDTOList.map(function (item) {
          return {
            breakfastNum: item.breakfastNum,
            salePrice: item.salePrice,
            saleDate: item.saleDate,
            currency: item.baseCurrency,
            basePrice: item.basePrice,
            fullPrice: item.salePrice,
          }
        })
      },

      /*model*/
      checkaddation(additem, e){
        var _t = this
        var $this = $(e.target).parent().parent()
        var _this = $this.find('.iconflag')
        var next = $this.next()
        //console.log(_this.hasClass('icon-Artboard9'));
        if (_this.hasClass('icon-Artboard9')) {
          _this.addClass('icon-Artboard11').removeClass('icon-Artboard9')
          if (_this.hasClass('icon-Artboard11')) {
            _t.req_booking.additionChargeDTOList.forEach(function (val, idx) {
              if (val.type == additem.type) {
                _t.req_booking.additionChargeDTOList.splice(idx, 2)
              }
            })
            next.hide();
          }
        } else {
          _this.addClass('icon-Artboard9').removeClass('icon-Artboard11')
          if (_this.hasClass('icon-Artboard9')) {
            _t.req_booking.additionChargeDTOList.push(additem)
            next.show()
          }
        }
      },

      checkaddation2(e){
        var _t = this
        var $this = $(e.target).parent().parent()
        var _this = $this.find('.iconflag')
        var next = $this.next()
        next.toggle();
      },

      callTel(){
        var _t = this;
        getnotice('<span><p>若需5间以上 请联系酒店</p></span><sapn id="tel">Call</sapn>', 3000,'notice-skin')
        setTimeout(function () {
          $('#tel').click(function () {
            window.location.href = 'tel:' + _t.indexDate.telePhone
          })
        }, 20)
      },

      // txtCall(){
      //   // console.log(this.indexDate.telePhone);
      //   window.location.href = 'tel:' + this.indexDate.telePhone
      // },

      costDetail(){
        this.iScostDetail = true;
        var str = ''
      },

      destinSome(){
        var _t = this;
        this.isDestinSome = true
        this.tempArr = JSON.parse(JSON.stringify(this.req_booking.additionChargeDTOList));
        //console.log(this.tempArr);
        setTimeout(function () {
          $('.ds-scoll .badChose').find('.iconflag').addClass('icon-Artboard11').removeClass('icon-Artboard9')
          $('.ds-scoll .badChose .badChose_body').hide()
          _t.req_booking.additionChargeDTOList.forEach(function (val, idx) {
            $('.ds-scoll .badChose').each(function (idx, vals) {
              //console.log($(this).attr('data-type'), '选中附加项');
              if (val.type == $(vals).attr('data-type')) {
                $('.ds-scoll .badChose').eq(idx).find('.iconflag').addClass('icon-Artboard9').removeClass('icon-Artboard11')
                $('.ds-scoll .badChose').eq(idx).find('.badChose_body').show()
              }
            })
          })
        }, 500)

      },
      closeDestinSome(){
        this.req_booking.additionChargeDTOList = this.tempArr
        this.isDestinSome = false
      },

      /*others*/
      chgFill(){
        var _this = $(this.$refs.sw_ctr);
        event.stopPropagation();
        if (!_this.hasClass('on')) {
          _this.children('.btn').css('margin-left', '0.34rem');
          _this.children('.base_aux').css('opacity', '0');
          _this.children('.base').css('background', '#4BD663');
          _this.parents('.orderFill_p02').next('.mnore_Div').show();
        } else {
          _this.children('.btn').css('margin-left', '0.002rem');
          _this.children('.base_aux').css('opacity', '1');
          _this.children('.base').css('background', '#f3f3f3');
          _this.parents('.orderFill_p02').next('.mnore_Div').hide();
        }
        _this.toggleClass('on');
      },

      cancel(){
        this.$router.go(-1)
      },
    }
  }
</script>



